.basket {
    position: fixed;
    top: 90px;
    right: 0;
    bottom: 0;
    width: 15%;
    background-color: rgba(109, 103, 103, 0.5);
    padding: 20px;
    color: white;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
    z-index: 9999;
}

.basket::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    z-index: -1;

}

.basket img {
    width: 90px;
    height: auto;
    border-radius: 5px;
}

.cart-logo {
    position: absolute;
    top: 10px;
    right: 10px;
}

.basketButtons {
    background-color: rgb(51, 51, 51);
    color: whitesmoke;
    border-radius: 5px;
    padding: 10px;
    margin: 0px;
    margin: 10px;
}

.basketButtons:hover {
    filter: brightness(1.2);
    scale: 1.1;
}

.basketItems {
    margin-top: 100px;
    font-size: 12px;

    list-style: none;
}

.modal-content {
  max-height: 1000px;
  overflow-y: scroll;
  scrollbar-width:none;
}